
.commission-wrapper {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 100;
}
.commission-wrapper .scroll-conntent {
  position: absolute;
  top: 130px;
  bottom: 0px;
  overflow: hidden;
  width: 100%;
}
.commission-wrapper .commission_wrapper {
  width: 100%;
  height: 80px;
  background-image: url("~@/common/image/my-co.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  padding-top: 20px;
}
.commission-wrapper .commission_wrapper div {
  width: 100%;
  height: 30px;
  padding: 0px 10px;
  text-align: center;
  font-size: 18px;
  color: #fff;
  font-weight: 700;
}
.commission-wrapper .commission_wrapper div span {
  padding: 0px 10px;
  font-size: 15px;
}


