
#pOrderSuccess {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 100;
  background-color: #edf1f5;
}
#pOrderSuccess #scroll {
  position: absolute;
  top: 40px;
  bottom: 50px;
  left: 0;
  right: 0;
}
#pOrderSuccess #scroll .body {
  margin-top: 6px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#pOrderSuccess #scroll .body >img {
  width: 64px;
  height: 64px;
  margin: 50px 0 20px 0;
}
#pOrderSuccess #scroll .body >p {
  font-size: 16px;
  color: #333;
}
#pOrderSuccess #scroll .body .btn-content {
  display: flex;
  justify-content: space-around;
  height: 34px;
  width: 80%;
  margin-top: 40px;
}
#pOrderSuccess #scroll .body .btn-content >span {
  width: 90px;
  height: 34px;
  display: inline-block;
  line-height: 34px;
  text-align: center;
  color: #edf1f5;
}
#pOrderSuccess #scroll .body .btn-content .checkOrder {
  background-color: #fe8921;
}
#pOrderSuccess #scroll .body .btn-content .goOrder {
  background-color: #e10101;
}


