
.work-room-info {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 100;
}
.work-room-info .mint-cell {
  min-height: 40px;
}
.work-room-info .mint-cell .mint-cell-title {
  width: auto;
  margin-left: 10px;
}
.work-room-info .mint-cell .mint-cell-title .mint-cell-text {
  font-size: 14px;
}
.work-room-info .mint-cell .mint-cell-value {
  margin-right: 10px;
}
.work-room-info .mint-cell .mint-field-core {
  font-size: 14px;
}
.work-room-info .mint-cell .mint-cell-wrapper {
  width: 100%;
  background-position: bottom right;
  padding: 0px;
}
.work-room-info .splith {
  width: 100%;
  padding: 4px 6px;
  background: #eff3f6;
}
.work-room-info .splith .pro-info {
  color: #f00;
  width: 100%;
  height: 36px;
  margin: 4px 0px;
  border: 1px solid #f4a460;
  background-color: #ffebe1;
  font-size: 14px;
  line-height: 36px;
  text-align: center;
}


