
#order {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 100;
  background-color: #edf1f5;
}
#order #scroll {
  position: absolute;
  top: 40px;
  bottom: 50px;
  left: 0;
  right: 0;
}
#order #scroll .scroll-content {
  margin-top: 6px;
  background-color: #fff;
}
#order .mint-popup-bottom:not(.mint-datetime) {
  width: 100%;
  height: auto;
}
#order .footer {
  height: 40px;
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: -1;
}
#order .footer .submit {
  width: 100%;
  height: 100%;
  display: block;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
  background-color: #d93f30;
  color: #fff;
}


