
.top-content {
  height: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.top-content .product-name {
  margin-left: 4px;
  font-size: 14px;
  color: #000;
}
.top-content .product-time {
  width: 70px;
  font-size: 12px;
  color: #686868;
}
.bottom-content {
  display: flex;
  height: calc(100px - 40px);
  justify-content: space-around;
  align-items: center;
}
.bottom-content .span-red {
  color: #f00;
}
.bottom-content div {
  height: 90%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.bottom-content div >span {
  font-size: 13px;
}
.bottom-content div:last-child >span:last-child {
  color: #686868;
}
.bottom-content div:last-child >span:first-child {
  color: #f00;
}


