
.custom-order {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 1;
}
.custom-order .mint-navbar .mint-tab-item {
  padding: 8px;
}
.custom-order .mint-tab-item-label {
  font-size: 13px;
}
.custom-order .scroll-conntent {
  position: absolute;
  top: 88px;
  bottom: 5px;
  overflow: hidden;
  width: 100%;
}
.custom-order .order-detail {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0px;
  background: #fff;
  z-index: 1;
}


