
.mint-header {
  color: #000;
  background-color: #fff;
  height: 50px;
}
.mint-cell {
  min-height: 40px;
}
.mint-cell-title {
  width: auto;
  margin-left: 10px;
}
.mint-cell-title .mint-cell-text {
  font-size: 13px;
}
.mint-cell-value {
  margin-right: 10px;
}
.mint-field-core {
  font-size: 13px;
}
.mint-cell-wrapper {
  width: 100%;
  background-position: bottom right;
  padding: 0px;
}


