
#hotProducts {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 100;
  background-color: #edf1f5;
}
#hotProducts #scroll {
  position: absolute;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
}
#hotProducts #scroll .body {
  margin-top: 6px;
  background-color: #fff;
}
#hotProducts .footer {
  height: 40px;
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: -1;
}


