
#pulldown .bankPicker {
  position: fixed;
  left: 0;
  right: 0;
  background-color: #fff;
  z-index: 2017;
  backface-visibility: hidden;
}
#pulldown .bankPicker .mint-datetime {
  height: 40px;
  line-height: 40px;
  overflow: hidden;
  text-align: center;
  border-bottom: solid 1px #eaeaea;
}
#pulldown .bankPicker .mint-datetime .cancel {
  float: left;
  padding: 0 20px;
  color: #26a2ff;
}
#pulldown .bankPicker .mint-datetime .confirm {
  float: right;
  padding: 0 20px;
  color: #26a2ff;
}
#pulldown .moveIn {
  bottom: 0;
  animation: mymove-in 0.2s 1;
}
#pulldown .moveOut {
  bottom: -220px;
  animation: mymove-out 0.2s 1;
}
@-moz-keyframes mymove-out {
from {
    bottom: 0;
}
to {
    bottom: -220px;
}
}
@-webkit-keyframes mymove-out {
from {
    bottom: 0;
}
to {
    bottom: -220px;
}
}
@-o-keyframes mymove-out {
from {
    bottom: 0;
}
to {
    bottom: -220px;
}
}
@keyframes mymove-out {
from {
    bottom: 0;
}
to {
    bottom: -220px;
}
}
@-moz-keyframes mymove-in {
from {
    bottom: -220px;
}
to {
    bottom: 0;
}
}
@-webkit-keyframes mymove-in {
from {
    bottom: -220px;
}
to {
    bottom: 0;
}
}
@-o-keyframes mymove-in {
from {
    bottom: -220px;
}
to {
    bottom: 0;
}
}
@keyframes mymove-in {
from {
    bottom: -220px;
}
to {
    bottom: 0;
}
}


