
#recommend {
  position: relative;
  width: 100%;
  height: 100%;
}
#recommend .recommend-content {
  position: absolute;
  top: 0;
  bottom: 46px;
  background: #fff;
  overflow: hidden;
  width: 100%;
/*
     * 
     */
}
#recommend .recommend-content .nav-func {
  padding-top: 18px;
  background-color: #eff3f6;
}
#recommend .recommend-content .nav-func .ofh {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
#recommend .recommend-content .nav-func .ofh li {
  width: 33%;
  text-align: center;
  margin-bottom: 19px;
  font-size: 14px;
}
#recommend .recommend-content .nav-func .ofh li img {
  width: 48px;
  padding-bottom: 6px;
}
#recommend .recommend-content .advertising {
  position: relative;
  width: 100vw;
  height: 86px;
  border-top: 8px solid #eff3f6;
  border-bottom: 8px solid #eff3f6;
  top: -4px;
}
#recommend .recommend-content .newRecommendProducts-content {
  position: relative;
  top: -4px;
}
#recommend .recommend-content .slider-wrapper {
  height: 180px;
  width: 100%;
  overflow: hidden;
}
#recommend .recommend-content .slider-wrapper img {
  height: 190px;
  width: 100%;
}
#recommend .recommend-content .recommend-list .list-title {
  height: 65px;
  line-height: 65px;
  text-align: center;
  font-size: 14px;
  color: #ffcd32;
}
#recommend .recommend-content .recommend-list .item {
  display: flex;
  box-sizing: border-box;
  align-items: center;
  padding: 0 20px 20px 20px;
}
#recommend .recommend-content .recommend-list .item .icon {
  flex: 0 0 60px;
  width: 60px;
  padding-right: 20px;
}
#recommend .recommend-content .recommend-list .item .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  line-height: 20px;
  overflow: hidden;
  font-size: 14px;
}
#recommend .recommend-content .recommend-list .item .text .name {
  margin-bottom: 10px;
}
#recommend .recommend-content .loading-container {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}


