
.rank {
  position: relative;
  width: 100%;
  height: 100vh;
}
.rank .rank-context {
  position: absolute;
  bottom: 46px;
  width: 100%;
  top: 0px;
  overflow: hidden;
  z-index: 2;
  background-image: url("~@/common/image/workroom-backimg.png");
  background-size: 100% 100%;
}
.rank .rank-context .logo {
  width: 14%;
  height: 6%;
  background-image: url("~@/common/image/lcs-logo.png");
  background-size: 100% 100%;
  margin: 36px 20px 0px auto;
}
.rank .rank-context .xuanchuan {
  display: block;
  width: 90%;
  height: 26%;
  margin: 0px auto 10px auto;
}
.rank .rank-context .rank-tubiaoqu {
  display: flex;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.rank .rank-context .rank-tubiaoqu .tubiao-flex {
  flex: 0 0 50%;
}
.rank .rank-context .rank-tubiaoqu .tubiao-flex .tubiao {
  width: 85px;
  height: 85px;
  background: #fff;
  border-radius: 16px;
  text-align: center;
  margin: 0px auto 30%;
  padding-top: 12px;
}
.rank .rank-context .rank-tubiaoqu .tubiao-flex .tubiao img {
  width: 48px;
}
.rank .rank-context .rank-tubiaoqu .tubiao-flex .tubiao span {
  display: block;
  font-size: 13px;
  margin-top: 5px;
}


