
.settings-all-0 {
  position: relative;
  width: 100%;
  background: #edf1f5;
  z-index: 2;
/*  */
/*  */
/*  */
}
.settings-all-0 .setting-header {
  height: calc(143px + 14px);
  width: 100%;
  padding-left: 18px;
  position: relative;
  background: url("../../common/image/my_settings_header_background.png") center center;
  background-color: #f00;
  background-size: cover;
  border-bottom: solid 1px #dcdcdc;
}
.settings-all-0 .setting-header .content-wrapper {
  padding: 24px 12px 18px 18px;
  font-size: 0;
}
.settings-all-0 .setting-header .content-wrapper .avatar {
  vertical-align: top;
  display: inline-block;
}
.settings-all-0 .setting-header .content-wrapper .avatar .header_avatar {
  border-radius: 40px;
}
.settings-all-0 .setting-header .content-wrapper .content {
  align-content: center;
  display: inline-block;
  font-size: 14px;
  margin-left: 16px;
  color: #fff;
}
.settings-all-0 .setting-header .content-wrapper .content .av-name {
  margin-top: 10px;
  font-size: 14px;
  margin-bottom: 10px;
}
.settings-all-0 .setting-header .content-wrapper .content .money-all {
  padding: 2px 4px;
  font-size: 12px;
  border: 1px solid #ffde00;
  border-radius: 12px;
}
.settings-all-0 .right-icon {
  position: absolute;
  right: 18px;
  top: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.settings-all-0 .right-icon .ri-setting {
  margin-right: 16px;
  font-size: 16px;
  color: #fff;
  display: inline-block;
}
.settings-all-0 .right-icon .ri-message {
  display: inline-block;
}
.settings-all-0 .setting-order {
  position: relative;
  height: 128px;
  width: 100vw;
  background: #fff;
/*  */
/*  */
}
.settings-all-0 .setting-order .view-all-orders {
  position: absolute;
  font-size: 10px;
  color: #666;
  top: 6px;
  right: 4px;
}
.settings-all-0 .setting-order .icon {
  position: absolute;
  top: -13px;
  left: calc(50% - 12px);
  width: 24px;
  height: 27px;
}
.settings-all-0 .setting-order .my-order {
  width: 100vw;
  height: calc(128px - 84px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.settings-all-0 .setting-order .my-order > hr {
  flex: 1;
  color: #edeeef;
}
.settings-all-0 .setting-order .my-order > span {
  margin: 0 6px;
  font-size: 14px;
  color: #666;
}
.settings-all-0 .setting-order .order-items {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 84px;
}
.settings-all-0 .setting-order .order-items .order-items-tab {
  height: 80%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.settings-all-0 .setting-order .order-items .order-items-tab > span {
  font-size: 10px;
  color: #303030;
}
.settings-all-0 .my-owns {
  margin: 12px 0;
  background: #fff;
  height: 172px;
  border-bottom: solid 1px #dcdcdc;
/* cell */
}
.settings-all-0 .my-owns > div {
  border-bottom: 1px solid #edeeef;
}
.settings-all-0 .my-owns .mint-cell-text {
  font-size: 14px;
}


