
#prodQueryModal {
  height: 100%;
  width: 100%;
  position: relative;
}
#prodQueryModal >.scroll-conntent {
  width: 100%;
  position: absolute;
  overflow: hidden;
  bottom: 34px;
  top: 0;
}
#prodQueryModal >.scroll-conntent >div >div {
  padding: 16px;
  border-bottom: 1px solid #bfbfbf;
}
#prodQueryModal >.scroll-conntent >div >div .title {
  font-size: 14px;
  color: #333;
  margin-bottom: 16px;
}
#prodQueryModal >.scroll-conntent >div >div >div >span {
  display: inline-block;
  padding: 4px 8px;
  margin: 6px;
  border: 1px solid #bfbfbf;
  border-radius: 16px;
}
#prodQueryModal >.scroll-conntent >div >div >div .ActiveIndex {
  background-color: #f00;
  color: #fff;
}
#prodQueryModal .footer-btn-content {
  height: 34px;
  width: 100vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 0;
}
#prodQueryModal .footer-btn-content >span {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
}
#prodQueryModal .footer-btn-content .reset {
  background: #d8d8d8;
  color: #333;
}
#prodQueryModal .footer-btn-content .confirm {
  background: #e10101;
  color: #fff;
}


