
.all-content {
  position: relative;
  width: 100%;
  height: 100%;
  background: #edf1f5;
  z-index: 100;
}
.all-content .header {
  border-bottom: solid 5px #eff3f6;
}
.all-content .page-navbar {
  height: calc(100% - 55px);
  position: relative;
/*  */
}
.all-content .page-navbar .scroll-content {
  position: absolute;
  top: 56px;
  bottom: 0;
  overflow: hidden;
  width: 100%;
}
.all-content .page-navbar .scroll-content .textDiv {
  height: 100px;
}
.all-content .page-navbar .scroll-content .mt-tab-contailer {
  left: 50%;
  margin-left: -100px;
}


