
.my-commission-info-wrapper {
  width: 100%;
  height: 160px;
}
.my-commission-info-wrapper .my-commission-info-header-content {
  border: solid 1px #eff3f6;
  height: 40px;
  display: flex;
  align-items: center;
  font-size: 15px;
}
.my-commission-info-wrapper .my-commission-info-header-content div {
  flex: 1;
  padding-left: 10px;
}
.my-commission-info-wrapper .my-commission-info-header-content div span {
  color: #f00;
}
.my-commission-info-wrapper .my-commission-info-header-content div:last-child {
  text-align: right;
  padding-right: 10px;
}
.my-commission-info-wrapper .my-commission-info-bottom-content >div {
  font-size: 14px;
  height: 30px;
  line-height: 30px;
}
.my-commission-info-wrapper .my-commission-info-bottom-content >div >span {
  padding-left: 10px;
}


