
#pDetails {
  position: relative;
  height: 100%;
  width: 100%;
  z-index: 100;
  background-color: #edf1f5;
}
#pDetails .scroll-conntent {
  position: absolute;
  bottom: 40px;
  top: 40px;
}
#pDetails .scroll-conntent >div >div {
  margin: 6px 0;
  background-color: #fff;
}
#pDetails .scroll-conntent >div >div >div:first-of-type {
  border-bottom: 1px solid rgba(104,104,104,0.3);
}
#pDetails .scroll-conntent >div .header-title {
  font-size: 16px;
  color: #000;
  margin: 14px 0 8px 0;
}
#pDetails .scroll-conntent >div p,
#pDetails .scroll-conntent >div span,
#pDetails .scroll-conntent >div a {
  font-size: 14px;
  color: #000;
}
#pDetails .scroll-conntent >div .title {
  color: #686868;
  margin: 8px 0;
}
#pDetails .scroll-conntent >div .red-span {
  color: #d93f30;
}
#pDetails .scroll-conntent >div .pro-header-content {
  height: 36px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
#pDetails .scroll-conntent >div .pro-header-content .copy {
  position: absolute;
  right: 14px;
}
#pDetails .scroll-conntent >div .body-content {
  padding: 10px 6px;
}
#pDetails .scroll-conntent >div .p-presentation .header-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60px;
}
#pDetails .scroll-conntent >div .p-presentation .header-content >img {
  width: 44px;
  height: 14px;
}
#pDetails .scroll-conntent >div .p-presentation .body-content .data-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}
#pDetails .scroll-conntent >div .p-presentation .body-content .data-content >div {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#pDetails .scroll-conntent >div .p-presentation .body-content .data-content >div >span {
  margin: 7px 0;
}
#pDetails .scroll-conntent >div .p-presentation .body-content .annotation {
  color: #d93f30;
  line-height: 20px;
}
#pDetails .scroll-conntent >div .p-basic-information .body-content >div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#pDetails .scroll-conntent >div .p-superiority .body-content >p,
#pDetails .scroll-conntent >div .p-collect-account .body-content >p,
#pDetails .scroll-conntent >div .risk-control .body-content >p,
#pDetails .scroll-conntent >div .source-repayment .body-content >p {
  line-height: 22px;
  margin: 6px 0;
}
#pDetails .recommend-content {
  height: 40px;
  position: absolute;
  bottom: 0;
  display: flex;
  width: 100vw;
  background-color: #fff;
  z-index: 101;
}
#pDetails .recommend-content >a {
  flex: 1;
  line-height: 40px;
  text-align: center;
  color: #464545;
}
#pDetails .recommend-content .subscribe {
  color: #fff;
  background-color: #d93f30;
}


