
.generalize-wrapper {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 100;
  background-image: url("~@/common/image/lcs_tuiguang.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.generalize-wrapper .logo {
  position: absolute;
  width: 14%;
  top: 4vh;
  right: 3vw;
  height: 6%;
  background-image: url("/static/img/lcs-logo.3133185.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.generalize-wrapper .generalize-avatar {
  width: 90px;
  height: 90px;
  margin: 0px auto 10px auto;
  border-radius: 50%;
  background: #fff;
  overflow: hidden;
}
.generalize-wrapper .generalize-content {
  text-align: center;
  color: #fff;
}
.generalize-wrapper .generalize-content div {
  margin-top: 14px;
}
.generalize-wrapper .generalize-content .name-class {
  font-size: 15px;
}
.generalize-wrapper .generalize-content .tel-class {
  font-size: 15px;
}
.generalize-wrapper .generalize-content .des-class {
  font-size: 13px;
  line-height: 22px;
  text-align: left;
  padding: 0px 10px;
}
.generalize-wrapper .generalize-content .tuiguangfangshi table {
  margin: auto;
}
.generalize-wrapper .generalize-content .tuiguangfangshi table tr td {
  height: 44px;
  line-height: 44px;
  text-align: left;
  font-size: 14px;
}
.generalize-wrapper .mint-header {
  background: none;
  height: 45px;
}
.generalize-wrapper .mint-header-button.is-left {
  color: #fff;
}


